import { Link } from "gatsby"
import React, { Component } from 'react';

class Header extends Component {
    constructor() {
        super();
        this._handleScroll = this._handleScroll.bind(this);
        this._handleClick = this._handleClick.bind(this);
    }

    _handleClick() {
        document.getElementsByClassName("menu-mobile_icon")[0].classList.toggle("active");
        document.querySelector(".header_block .menu_block").classList.toggle("active");
        document.querySelector("footer, main").classList.toggle("dNone");
        document.getElementsByClassName("logo_block")[0].classList.toggle("d-none");
    }

    _handleScroll() {
        const sticky = document.getElementsByClassName("header_block")[0];
        const scroll = window.scrollY;

        if (scroll >= 40) {
            sticky.classList.add("fixed");
        } else {
            sticky.classList.remove("fixed");
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this._handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this._handleScroll);
    }

    render() {
        return (
            <header className="header_block">
                <div className="container d-flex align-items-center position-relative">

                    <nav className="menu_block w-100">
                        <ul className="list-inline menu_block-list">
                            <li className="list-inline-item"><a href="https://devarman.github.io/getsby/blog/">Blog</a></li>
                        </ul>
                    </nav>
                    <div className="menu-mobile_icon" onClick={this._handleClick}>
                        <span></span>
                    </div>
                </div>
            </header>
        );
    }
}

export default Header;
