import React from "react"
import {graphql,  StaticQuery} from "gatsby"

import Layout from "../components/layout"

const IndexPage = () => (
    <StaticQuery
        query={graphql`
    query GetHomePage {
      wordpressPage(id: {eq: "f39c2fcc-afc9-5ae0-b102-921236793736"}) {
        title
        content
      }
    }
  `}
        render={data => (
            <Layout>
                <div style={{
                    margin: "0 auto",
                    fontSize: "32px",
                    width: "600px"
                }} dangerouslySetInnerHTML={{__html: data.wordpressPage.content}}>
                </div>
            </Layout>)
        }
    />
);

export default IndexPage
